      <p>copyright2011-2017  </p>
      <p><a href="http://www.miitbeian.gov.cn/ ">ICP16078801-1</a></p>
    </div>
  </section>
</template>

<script>
export default {
  name: 'comFoot',
  data (){
    return {

    }
  },
  methods: {
    //
    singOut (){

      // sessionStorage.seslotoken = ''; // token
      // sessionStorage.seslouserph = ''; // 
      sessionStorage.seslomemberInfo = ''; // ID
      this.$store.commit('minus', {
        addToken: '',
        addPhone: '',
        addMemberID: ''
      });
      // this.$store.commit('addBuyProductInfo', '');//
      // this.$router.replace({path: '/login'});
      location.reload();
    },
  },
}
</script>

<style scoped>
  @import './Css/comfoot.css';
</style>


